import React, { Component } from "react";
import CrossFade from "../components/Crossfade";
import { RiFullscreenExitFill } from 'react-icons/ri'
export default class Gallery extends Component {
    constructor(props) {
        super(props);
        this.data = props.data
    }

    state={
        'clicked': -1
    }

    exitImage() {
        if (this.state.clicked >= 0)  this.setState({clicked: -1})
    }

    componentDidMount() {
        this.escapeListener = document.addEventListener('keydown', ({key}) => {
            if (key === 'Escape' && this.state.clicked > -1) this.setState({clicked: -1})
        })
    }

    componentWillUnmount() {
        document.removeEventListener(this.escapeListener)
    }

    

    render() {
        document.body.style.overflow = (this.state.clicked > -1) ? "hidden" : ""

        return <div style={{
            textAlign: 'center',
            lineHeight: 0,
            backgroundColor: 'rgb(30, 30, 30)',
            paddingTop: 'calc(12vh)',
            transformBox: 'content-box',
            minHeight: '100vh'
        }} onClick={(e) => {
                this.exitImage()
                e.stopPropagation()
            }}>
            {this.data.map((path, idx) => (<div style={{
                display: 'inline-block',
                width: '45vw',
                margin: '10px',
                verticalAlign: 'top'
            }} key={'item' + idx}>
                <img src={process.env.PUBLIC_URL + path} alt='not found' onClick={() => this.setState({clicked: idx})} style={{
                    maxWidth: 'calc(45vw - 16px)',
                    maxHeight: 'calc(45vw - 16px)',
                    objectFit: 'contain',
                    verticalAlign: 'middle',
                    backgroundColor: 'white',
                    padding: '3px',
                }}/> </div>
            ))}

            <div style={{
                position: "fixed",
                top: '0', bottom: '0',
                left: '0', right: '0',
                margin: 'auto',
                pointerEvents: 'none',
                maxWidth: '90vw',
                maxHeight: '96vh',
            }} onKeyDown={(e) => console.log(e)} onClick={(e) => e.stopPropagation()}>
                <CrossFade 
                    img={this.state.clicked >= 0 ? this.data[this.state.clicked] : null}
                    img_style={{
                        border: '30px solid white',
                        maxWidth: 'calc(100% - 60px)',
                        maxHeight: 'calc(100% - 60px)',
                        width: null,
                        height: null,
                        zIndex: 3
                    }}
                > 
                </CrossFade>
                {this.state.clicked > -1 ? <div style={{
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    backgroundColor: 'rgb(10, 10, 10, .5)',
                    zIndex: 2
                }}> here </div> : ''}
            </div>
        </div>
    }
}