import React, { Component, createRef } from "react";
import CrossFade from "../components/Crossfade";

export default class Home extends Component{
    duration = 12000

    state = {
        photo: 0
    }

    render() {
        return <div style={{
            zIndex: -1,
            position: 'absolute',
            height: '100vh',
            width: '100vw'
        }}>
            <CrossFade img={this.data[this.state.photo]} />
        </div>
    }

    constructor(props) {
        super(props);
        this.data = props.data
        this.ref = createRef()
        
        for(let im in props.data) {
            const img = new Image();
            img.src = props.data[im];
        }
        setInterval(() => this.setState({photo: (this.state.photo + 1) % this.data.length}), this.duration)
    }
}