import React, { Component } from "react";

export default class CrossFade extends Component {
    duration = 500

    state = {
        im1: null,
        im2: null,
        counter: false
    }

    static getDerivedStateFromProps({img}, state) {
        state[state.counter ? 'im1' : 'im2'] = img
        state.counter = !state.counter
        return state
    }

    render() {
        const shared_style = (offset) => ({
            objectFit: 'cover',
            overflow: 'hidden',
            marginRight: 'auto',
            marginLeft: 'auto',
            display: 'block',
            position: 'absolute',
            animationName: offset ? 'fadein' : 'fadeout',
            animationDuration: this.duration + 'ms',
            animationFillMode: 'forwards',
            left: 0,
            right: 0,
            pointerEvents: offset ? 'all' : 'none',
            width: '100%',
            height: '100%'
        })

        return <div style={{ maxHeight: '100%', maxWidth: '100%' }}>
            {this.state.im1 ? <img src={process.env.PUBLIC_URL + this.state.im1} alt="" style={{...shared_style(!this.state.counter), ...this.props.img_style}}/> : null }
            {this.state.im2 ? <img src={process.env.PUBLIC_URL + this.state.im2} alt="" style={{...shared_style(this.state.counter), ...this.props.img_style}}/> : null }
            {this.props.children}
        </div>
    }
}